

import React from 'react';
import {Grid, Typography, Paper, useMediaQuery} from '@material-ui/core';
import classNames from 'classnames';
import htmlLogo from '../resources/html-5.svg';
import cssLogo from '../resources/css-3.svg';
import reactLogo from '../resources/react.svg';
import springBootLogo from '../resources/spring-boot.svg';
import springSecurityLogo from '../resources/spring.png';
import hibernateLogo from '../resources/hibernate.svg';
import mavenLogo from '../resources/maven.png';
import materialUILogo from '../resources/material-ui.png';
import tomcatLogo from '../resources/tomcat.png';
import thymeleafLogo from '../resources/thymeleaf.png';
import mysqlLogo from '../resources/mysql.svg';
import herokuLogo from '../resources/heroku.svg';
import javaLogo from '../resources/java.png';
import jsLogo from '../resources/js.png';
import matlabLogo from '../resources/matlab.png';
import eclipseLogo from '../resources/eclipse.png';
import githubLogo from '../resources/github-actions.svg';
import postmanLogo from '../resources/postman.png';
import VScodeLogo from '../resources/VScode.png';



const Skills = (props) =>{
    const classes = props.modifier1;
    const classes2 = props.modifier2;
    
    
    const matches = useMediaQuery('(min-width:800px)');
    const timeProps = matches ? 3: 12;

    
    
    console.log(timeProps);

    return (
        <>
            <Grid style={{padding: 0}}item xs={12}>
               
                    <Grid container justify="center" style={{paddingBottom:60}} className={classNames(classes2.introtext,classes2.defaultgrey)}>
                        <div id="Skills" style={{padding: 10}} />
                        <Typography className={classNames(classes2.heading,classes2.fontheading)} variant="h3">
                            
                            SKILLS<span className={classes2.defaultpink}>.</span>
                        </Typography>

                        <Grid container style={{padding: 0}} spacing={3} justify="center" >
                            <Grid item xs={timeProps} container justify="center">
                                <Paper style={{backgroundColor:"#212121"}}elevation={3}>
                                    <Typography className={classNames(classes2.paragraph, classes2.fontheading, classes2.defaultwhite, classes2.text_center)} variant = "h4">
                                        FRONTEND
                                    </Typography>
                                    <Grid container wrap="wrap" justify="center">
                                        <Grid container direction="column" className={classNames(classes2.clip,classes2.img_circle)} justify = "center" alignItems="center">
                                            <img className={classes.img_circle} src={htmlLogo} />
                                            <span className={classes.title_sub_tech}>HTML5</span>
                                        </Grid>
                                        <Grid container direction="column" className={classNames(classes2.clip,classes2.img_circle)} justify = "center" alignItems="center">
                                            <img className={classes.img_circle} src={cssLogo} />
                                            <span className={classes.title_sub_tech}>CSS3</span>
                                        </Grid>
                                        <Grid container direction="column" className={classNames(classes2.clip,classes2.img_circle)} justify = "center" alignItems="center">
                                            <img className={classes.img_circle} src={reactLogo} />
                                            <span className={classes.title_sub_tech}>React</span>
                                        </Grid>
                                        <Grid container direction="column" className={classNames(classes2.clip,classes2.img_circle)} justify = "center" alignItems="center">
                                            <img className={classes.img_circle} src={materialUILogo} />
                                            <span className={classes.title_sub_tech}>Material-UI</span>
                                        </Grid>
                                        <Grid container direction="column" className={classNames(classes2.clip,classes2.img_circle)} justify = "center" alignItems="center">
                                            <img className={classes.img_circle} src={thymeleafLogo} />
                                            <span className={classes.title_sub_tech}>Thymeleaf</span>
                                        </Grid>
                                    </Grid>
                                    
                                </Paper>
                            </Grid>
                            <Grid item xs={timeProps} container justify="center">
                                <Paper style={{backgroundColor:"#212121"}}elevation={3}>
                                    <Typography className={classNames(classes2.paragraph, classes2.fontheading, classes2.defaultwhite, classes2.text_center)} variant = "h4">
                                        BACKEND
                                    </Typography>
                                    <Grid container wrap="wrap" justify="center">
                                        <Grid container direction="column" className={classNames(classes2.clip,classes2.img_circle)} justify = "center" alignItems="center">
                                            <img className={classes.img_circle} src={springBootLogo} />
                                            <span className={classes.title_sub_tech}>Spring Boot</span>
                                        </Grid>
                                        <Grid container direction="column" className={classNames(classes2.clip,classes2.img_circle)} justify = "center" alignItems="center">
                                            <img className={classes.img_circle} src={springSecurityLogo} />
                                            <span className={classes.title_sub_tech}>Spring Security</span>
                                        </Grid>
                                        <Grid container direction="column" className={classNames(classes2.clip,classes2.img_circle)} justify = "center" alignItems="center">
                                            <img className={classes.img_circle} src={hibernateLogo} />
                                            <span className={classes.title_sub_tech}>Hibernate</span>
                                        </Grid>
                                        <Grid container direction="column" className={classNames(classes2.clip,classes2.img_circle)} justify = "center" alignItems="center">
                                            <img className={classes.img_circle} src={mavenLogo} />
                                            <span className={classes.title_sub_tech}>Maven</span>
                                        </Grid>
                                        <Grid container direction="column" className={classNames(classes2.clip,classes2.img_circle)} justify = "center" alignItems="center">
                                            <img className={classes.img_circle} src={tomcatLogo} />
                                            <span className={classes.title_sub_tech}>Tomcat</span>
                                        </Grid>
                                        <Grid container direction="column" className={classNames(classes2.clip,classes2.img_circle)} justify = "center" alignItems="center">
                                            <img className={classes.img_circle} src={mysqlLogo} />
                                            <span className={classes.title_sub_tech}>Mysql</span>
                                        </Grid>
                                        <Grid container direction="column" className={classNames(classes2.clip,classes2.img_circle)} justify = "center" alignItems="center">
                                            <img className={classes.img_circle} src={herokuLogo} />
                                            <span className={classes.title_sub_tech}>Heroku</span>
                                        </Grid>
                                    </Grid>
                                    
                                </Paper>
                            </Grid>
                            <Grid item xs={timeProps} container justify="center">
                                <Paper style={{backgroundColor:"#212121"}}elevation={3}>
                                    <Typography className={classNames(classes2.paragraph, classes2.fontheading, classes2.defaultwhite, classes2.text_center)} variant = "h4">
                                        LANGUAGES &amp; TOOLS
                                    </Typography>
                                    <Grid container wrap="wrap" justify="center">
                                        <Grid container direction="column" className={classNames(classes2.clip,classes2.img_circle)} justify = "center" alignItems="center">
                                            <img className={classes.img_circle} src={javaLogo} />
                                            <span className={classes.title_sub_tech}>Java</span>
                                        </Grid>
                                        <Grid container direction="column" className={classNames(classes2.clip,classes2.img_circle)} justify = "center" alignItems="center">
                                            <img className={classes.img_circle} src={jsLogo} />
                                            <span className={classes.title_sub_tech}>JavaScript</span>
                                        </Grid>
                                        <Grid container direction="column" className={classNames(classes2.clip,classes2.img_circle)} justify = "center" alignItems="center">
                                            <img className={classes.img_circle} src={matlabLogo} />
                                            <span className={classes.title_sub_tech}>Matlab</span>
                                        </Grid>
                                        <Grid container direction="column" className={classNames(classes2.clip,classes2.img_circle)} justify = "center" alignItems="center">
                                            <img className={classes.img_circle} src={eclipseLogo} />
                                            <span className={classes.title_sub_tech}>Eclipse</span>
                                        </Grid>
                                        <Grid container direction="column" className={classNames(classes2.clip,classes2.img_circle)} justify = "center" alignItems="center">
                                            <img className={classes.img_circle} src={VScodeLogo} />
                                            <span className={classes.title_sub_tech}>VS Code</span>
                                        </Grid>
                                        <Grid container direction="column" className={classNames(classes2.clip,classes2.img_circle)} justify = "center" alignItems="center">
                                            <img className={classes.img_circle} src={postmanLogo} />
                                            <span className={classes.title_sub_tech}>Postman</span>
                                        </Grid>
                                        <Grid container direction="column" className={classNames(classes2.clip,classes2.img_circle)} justify = "center" alignItems="center">
                                            <img className={classes.img_circle} src={githubLogo} />
                                            <span className={classes.title_sub_tech}>Github Workflows</span>
                                        </Grid>
                                    </Grid>
                        
                                </Paper>
                            </Grid>                            
                        </Grid>

                        

                    </Grid>
                </Grid>

        </>
    )
}

export default Skills;