import React from 'react';
import { makeStyles, createMuiTheme, MuiThemeProvider  } from "@material-ui/core/styles";
import {AppBar, Tabs, Tab, Grid, Divider, List, Typography, useMediaQuery,Drawer, ListItem, ListItemIcon, ListItemText, IconButton, Hidden} from "@material-ui/core";
import MenuIcon from '@material-ui/icons/Menu';
import Layout from '../../Layout/Layout';
import classNames from 'classnames';
import {Switch, BrowserRouter as Router, Route, Redirect} from 'react-router-dom'
import {HashLink as Link} from 'react-router-hash-link';
import Project from '../../Components/Project'
import Notes from '../../Components/Notes'
import ScopedCssBaseline from '@material-ui/core/ScopedCssBaseline';


const drawerWidth = 240;

const useStyles = makeStyles((theme) => ( {


  root: {
    padding: '0 16px',
    fontFamily: "Righteous",
  },
  primary: { 
      position:'fixed',
      top:'0',
      backgroundColor:'var(--mdc-theme-primary, #212121)',
      boxShadow: '0px 2px 4px -1px rgba(0,0,0,0.2), 0px 4px 5px 0px rgba(0,0,0,0.14), 0px 1px 10px 0px rgba(0,0,0,0.12)',
      boxSizing: 'border-box', 
      width:'100%',
  },
  title: {
    fontFamily: " 'Roboto Mono', monospace",
    
    [theme.breakpoints.down('sm')]:{
        marginLeft:'5px',
    },
    marginLeft:'40px',
  },
  customfontstyle: {
    color:'white',
    fontSize:'1.2rem',
    fontWeight: 500,
    minWidth:'20px',
   
    
  },
  tabsStyle: {
    marginRight:'24px',
  },menuButton: {
    marginRight:'40px',
    [theme.breakpoints.down('sm')]:{
        marginRight:'5px',
    },
    
    height: '48px',
    width: '48px',
    padding:0,
    paddingRight: 12,
    paddingLeft: 12,
  }, menuButtonStyle:{
      height:'1.5em',
      width:'1.5em',
      "&:hover": {
        opacity:0.5,
    },
  },drawerPaper: {
    width: drawerWidth,
  },toolbar: {
      minHeight:50,
  },drawertext:{
    fontFamily: "Righteous",
    "&:hover": {
        opacity:0.5,
    },
  },dividerColor:{
      background: "var(--mdc-theme-primary, #212121)",
  },



}));



const theme = createMuiTheme({
    breakpoints: {
      values: {
        xs: 0,
        sm: 600,
        md: 960,
        lg: 1280,
        xl: 1920,
        laptop: 1150,
      }, 
    },
    
  });



const NavigationItem = (props) => {

    const classes = useStyles();
   
    const handleChange = (event, newValue) =>{

        setSelectedTab(newValue);
        console.log(newValue);
    }
    const resetting = () =>{
        setSelectedTab(0);  
    }
    const [selectedTab, setSelectedTab] = React.useState(0);
    const [mobileOpen, setMobileOpen] = React.useState(false);

    const handleDrawerToggle = () => {
        setMobileOpen(!mobileOpen);
    };


    const reset = () => {
        window.location.href = '/';
    }
    const navlists1 = ["HOME", "ABOUT", "SKILLS", "EXPERIENCE", "EDUCATION"]
    const navlists2 = ["PROJECTS", "NOTES"];
    const navlinklists = ["/#Home", '/#About', '/#Skills', '/#Experience', '/#Education', '/Projects', '/Notes'];

    const handleMenu = (index) => {
        handleDrawerToggle();

        setSelectedTab(index);
    }

    
    
    const drawer = (

        <div>
            <div className={classes.toolbar} />
            <Divider classes={{root: classes.dividerColor}} />
            <List>
                {navlists1.map ((text,index) => (
                     <ListItem component={Link} smooth to = {navlinklists[index]} button key={text} onClick={() => {handleMenu(index)}}>
                        <ListItemText classes = {{primary: classes.drawertext}} primary={text} />
                     </ListItem>
                ))}
            </List>
            <Divider classes={{root: classes.dividerColor}}  />
            <List>
                {navlists2.map ((text,index) => (
                     <ListItem component={Link} smooth to = {navlinklists[navlists1.length + index]} button key={text} onClick={() => {handleMenu(navlists1.length+index)}}>
                        <ListItemText classes = {{primary: classes.drawertext}} primary={text} />
                     </ListItem>
                ))}
            </List>


        </div>


    )
    const matches = useMediaQuery('(min-width:1200px)');

    let navtab;

    if(matches){
        
        navtab = (
            <Grid className={classes.tabsStyle}>
                    
                <Tabs TabIndicatorProps= {{style: {height: "6px", backgroundColor: "#F8DAE9", transition: '0s'} }} className={classes.tabsStyle} value={selectedTab} onChange={handleChange} centered>
                    <Tab component={Link} smooth to='/#Home' className={classNames(classes.root, classes.customfontstyle)} label="Home" />
                    <Tab component={Link} smooth to='/#About' className={classNames(classes.root, classes.customfontstyle)} label="About" /> 
                    <Tab component={Link} smooth to='/#Skills' className={classNames(classes.root, classes.customfontstyle)} label="SKILLS" />
                    <Tab component={Link} smooth to='/#Experience' className={classNames(classes.root, classes.customfontstyle)} label="Experience" />
                    <Tab component={Link} smooth to='/#Education' className={classNames(classes.root, classes.customfontstyle)} label="Education" />
                    
                    <Tab component={Link} smooth to='/Projects' className={classNames(classes.root, classes.customfontstyle)} label="Projects" />
                    <Tab component={Link} smooth to='/Notes' className={classNames(classes.root, classes.customfontstyle)} label="NOTES" />
                </Tabs>
            
            </Grid>

        )
    }else{
        navtab = (
                <Grid>  
                    <IconButton 
                        color="inherit"
                        aria-label="open drawer"
                        className={classes.menuButton}
                        onClick={handleDrawerToggle}
                    >
                        <MenuIcon className={classes.menuButtonStyle}/>
                    </IconButton>
                    <Drawer 
                        anchor='right'
                        open = {mobileOpen}
                        onClose={handleDrawerToggle}
                        classes={{
                            paper: classes.drawerPaper,
                        }}>

                        {drawer}
                    </Drawer>
                </Grid>
        )
    }

    
    

    return (
        
        <Router>
            <AppBar elevation={0} className={classes.primary} position="static">
                <Grid justify={"space-between"} container>
                    <Grid style={{paddingBottom: 2, paddingTop: 2}}>
                        <Tab selected onClick = {reset} className={classNames(classes.title, classes.customfontstyle)} label="Moon Lee's Website"/>
                    </Grid>
                    {navtab}
                    
                    
                </Grid>
            </AppBar>
            
            
            <MuiThemeProvider theme={theme}>
                <ScopedCssBaseline>
                    <Switch>
                        <Route path="/Projects"> <Project /></Route>
                        <Route path="/Notes"> <Notes /></Route>
                        <Route path="/" exact> <Layout  tabselect={selectedTab} clicked={resetting} /> </Route>
                        

                    </Switch>
                </ScopedCssBaseline>
            </MuiThemeProvider>
        </Router>
        
    )

}

export default NavigationItem;