import React from 'react';
import {Grid, Typography, Link, makeStyles, TextareaAutosize, Paper} from '@material-ui/core';
import classNames from 'classnames';
import AndroidIcon from '@material-ui/icons/Android';
import BathtubIcon from '@material-ui/icons/Bathtub';
import BuildIcon from '@material-ui/icons/Build';
import SettingsIcon from '@material-ui/icons/Settings';
import UnderConstruct from '../resources/underconstruct.png';

const useStyles = makeStyles( (theme) => ({
    root: {
        fontSize: '3rem',
        fontWeight: 'bold',
        color:'black',
        marginTop: 100,
        height: '100%',
        
    }, message: {

        
        fontSize:'1.4rem',
    }, android:{

    }, underconstructionstyle:{
        maxWidth: '100%',
        height:'auto',
    }, paper:{
        padding: 10,
        backgroundColor: '#FAF3DD',
        border: '2px solid rgba(0, 0, 0, 0.87)',
        textAlign:'center',
    },

}));


const Notes = () =>{
    const classes = useStyles();

    return (

        <>
            <Grid direction = 'column' alignItems = "center" justify = "center" className = {classNames(classes.root)} container>
                
                <Paper className={classes.paper}>
                    <img alt = "construct" className = {classNames(classes.underconstructionstyle)} src={UnderConstruct} />
                    <Grid direction = 'row' Item>
                        <Typography className = {classNames(classes.message)}>
                            <i> Personal note section: Under construction...!</i>
                        </Typography>
                    </Grid>
                </Paper>
                
                
            </Grid>
        </>
            


    )


};


export default Notes;