

import React from 'react';
import {Grid, Typography, Link, makeStyles} from '@material-ui/core';
import classNames from 'classnames';
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward';


const useStyles = makeStyles( (theme) => ({
    footerheight:{
        textAlign:'center',
    },
    footfontstyle: {
        fontFamily: " 'Roboto Mono', monospace",
        color:'white',
        fontSize:'15px',
        fontWeight: 500,
        minWidth:'20px',
        height: '60px',
        padding:'20px',
        
    },

}));

const Footer = (props) => {
    const classes2 = props.modifier2;
    const classes1 = useStyles();
    return (
        <>
            <Grid style={{padding: 0}}item xs={12}>
                <Grid className={classNames(classes2.defaultblack, classes1.footerheight)}>
                    <Typography className = {classes1.footfontstyle} >
                        Copyright @ Moon Lee 2020
                    </Typography>
                </Grid>
            </Grid>
        </>


    )

}

export default Footer;