
import React from 'react';
import {Grid, Typography, Link, makeStyles} from '@material-ui/core';
import profilepicture from '../resources/Profile_Picture.PNG';
import classNames from 'classnames';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import IconButton from "@material-ui/core/IconButton"
import GitHubIcon from '@material-ui/icons/GitHub';


const useStyles = makeStyles( (theme) => ({
    iconopacity: {
        "&:hover": {
            opacity:0.5,
        },
    },
    textcenter:{
        textAlign:'center',
    },
    defaultbg:{
        backgroundColor: '#212121',
    },
}));


const About = (props) => {

    const classes = props.modifier1;
    const classes2 = props.modifier2;
    const classes1 = useStyles();


    
    return (
        <>
            
            <Grid style={{ padding: 0}}item xs={12}>
                    <Grid container justify="center" style={{paddingTop:100, paddingBottom:60}}className={classNames(classes2.introtext,classes1.defaultbg)}>
                        <img className={classes.profile} src={profilepicture} />
                        <Typography className={classNames(classes2.root,classes2.fontheading)} variant="h2">
                            Moon Lee
                        </Typography>
                        <Typography className={classNames(classes1.textcenter, classes2.subtitle,classes2.fontheading)} variant="h4">
                            Product Development Engineer in Dearborn, MI
                        </Typography>
                        <Typography className={classNames(classes2.subtitle1,classes2.fontheading)} variant="h4">
                            Software Developer/Hobbist
                        </Typography>
                    </Grid>
                    
                </Grid>
                
                <div id="About" style={{padding: 10}}/>
                <Grid container justify='center' style={{padding: 0, marginBottom:50}} >
                    
                    <Grid container justify="center">
                        <Typography  className={classNames(classes2.heading,classes2.fontheading)} variant="h3">
                            
                            ABOUT ME<span className={classes2.defaultpink}>.</span>
                        </Typography>
                    </Grid>
                    <Grid container style={{padding: 0}} direction="row" justify="center" wrap='wrap'>
                        <Grid item container justify="center" direction="column" alignItems="flex-end" className={classes2.custommaxwidth} >
                            <Typography className={classNames(classes2.paragraph, classes2.customwidth, classes2.fontpargraph)} variant="h6">
                                <i>Hello!</i> Welcome to my personal website. My name is Moon Lee and I am currently working for Ford Motor Company in Dearborn, Michigan as a product
                                development engineer in Electrified Powertrain Engineering divison. My main responsiblity is to lead and manage Tier1 supplier to design and deliver automotive 
                                traction DC-AC inverter systems for next generaiton HEV/EV vehicles.
                                
                                    
                            </Typography>
                            <Typography className={classNames(classes2.paragraph, classes2.customwidth, classes2.fontpargraph)} variant="h6">

                                Outside my normal work, I have a strong passion in coding and have been learning web app development during my spare time.
                                This website is primarily served as my online platform in which I can showcase my achievements, projects, notes, and much more.
                                
                            </Typography>
                        </Grid>
                        <Grid  item className={classes2.customwidth2}>
                            
                            <Typography className={classNames(classes2.paragraph, classes2.fontheading)} variant = "h4">
                                HIGHLIGHTS
                            </Typography>
                            <Typography className={classNames(classes2.paragraph, classes2.fontpargraph, classes2.lineheight)} variant="h5">
                                <li>Product Development Engineer at Ford - Top Achiever 2019 </li>  
                                <li>Hardware Engineer at Bosch - Top Performer 2017 </li>
                                <li>University of Michigan ECE Masters</li> 
                                <li>University of Toronto ECE Bachelors</li>                   
                            </Typography>
                            <Typography className={classNames(classes2.paragraph, classes2.fontheading)} variant = "h4">
                                CONTACTS
                            </Typography>
                            <Typography className={classNames(classes2.paragraph, classes2.fontpargraph,  classes2.defaultred)} variant="h5">
                                Resume: <Link href="#">
                                    View
                                </Link>
                            </Typography>
                            <Typography className={classNames(classes2.paragraph, classes2.fontpargraph,  classes2.defaultred)} variant="h5">
                                Email: <Link href="mailto:leemoon@umich.edu">
                                    leemoon@umich.edu
                                </Link>
                            </Typography>
                            <Typography className={classNames(classes2.paragraph)} variant="h5">
                                <IconButton
                                    className={classes2.icons}
                                    aria-label="Linkedin.com"
                                    onClick={() => window.open("https://www.Linkedin.com/in/moon-lee-003baa86/")}>
                                    <LinkedInIcon className= {classes1.iconopacity} style={{fill: '#1976d2'}} fontSize="large" />
                                </IconButton>
                                <IconButton
                                    className={classes2.icons}
                                    aria-label="Github.com"
                                    onClick={() => window.open("https://github.com/mlee242")}>
                                    <GitHubIcon className= {classes1.iconopacity} style={{fill: 'black'}} fontSize="large" />
                                </IconButton>
                            </Typography>
                
                        </Grid>
                    </Grid>
                </Grid>

        </>


    ) 




}

export default About;